@import "https://fonts.googleapis.com/css2?family=Lora:wght@400;700&family=Outfit:wght@300;400;500;600&display=swap";
@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&family=Roboto:wght@300;400;500;700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&family=Roboto:wght@400;700&display=swap";
:root {
  --color-primary-blue: #0054a6;
  --color-accent-green: #8dc63f;
  --color-dark-navy-outline: #003b71;
  --color-white: #fff;
  --color-background-light: #f4f8ff;
  --color-background-dark-3d: #002a52;
  --color-secondary-bg: #e6f0ff;
  --color-text-main: #1d1d1d;
  --color-text-dark-blue: var(--color-dark-navy-outline);
  --color-text-light: var(--color-white);
  --font-heading: "Outfit", sans-serif;
  --font-body: "Outfit", sans-serif;
  --font-elegant: "Playfair Display", serif;
  --transition-speed: .3s ease-in-out;
  --border-radius: 8px;
  --box-shadow-light: 0 2px 10px #0000001a;
  --box-shadow-medium: 0 5px 15px #00000026;
  --box-shadow-floating: 0 10px 30px #0003;
}

* {
  box-sizing: border-box;
  font-family: var(--font-body);
  margin: 0;
  padding: 0;
}

body {
  background: var(--color-background-light);
  color: var(--color-text-main);
  overflow-x: hidden;
}

header {
  background: var(--color-white);
  box-shadow: var(--box-shadow-light);
  z-index: 1000;
  justify-content: space-between;
  align-items: center;
  padding: 0 200px;
  display: flex;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
}

.logo {
  color: var(--color-primary-blue);
  align-items: center;
  gap: 10px;
  font-size: 1.5rem;
  font-weight: bold;
  display: flex;
}

.logo img {
  height: 125px;
}

.contact-info-desktop {
  color: var(--color-text-main);
  gap: 20px;
  font-size: .9rem;
  display: flex;
}

.contact-info-desktop p {
  white-space: nowrap;
}

.floating-navbar-container {
  max-width: 800px;
  box-shadow: var(--box-shadow-floating);
  background: var(--color-primary-blue);
  z-index: 999;
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  padding: 10px 20px;
  display: flex;
  position: relative;
}

nav {
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  display: flex;
}

nav .nav-links {
  flex-wrap: wrap;
  flex-grow: 0;
  justify-content: center;
  padding: 0;
  display: flex;
}

.nav-link {
  color: var(--color-white);
  transition: background var(--transition-speed);
  margin: 0 15px;
  padding: 8px 0;
  text-decoration: none;
  position: relative;
  overflow: hidden;
}

.nav-link:after {
  content: "";
  background-color: var(--color-accent-green);
  width: 0;
  height: 2px;
  transition: width var(--transition-speed);
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-link:hover:after, .nav-link.active:after {
  width: 100%;
}

.floating-navbar-container .contact-info-mobile {
  display: none;
}

.hamburger-menu {
  cursor: pointer;
  z-index: 1001;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 16px;
  display: none;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.hamburger-menu span {
  background-color: var(--color-white);
  width: 100%;
  height: 2px;
  transition: all .3s;
  display: block;
}

.hamburger-menu.active span:first-child {
  transform: translateY(7px)rotate(45deg);
}

.hamburger-menu.active span:nth-child(2) {
  opacity: 0;
}

.hamburger-menu.active span:nth-child(3) {
  transform: translateY(-7px)rotate(-45deg);
}

.slider {
  display: none;
}

.canvas-container {
  background: var(--color-background-dark-3d);
  width: 100%;
  height: 600px;
  box-shadow: var(--box-shadow-medium);
  margin-top: 20px;
  position: relative;
  overflow: hidden;
}

.animation-content-wrapper {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  width: 100%;
  height: 100%;
  padding: 0 30px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

canvas {
  flex-grow: 1;
  max-width: 65%;
  display: block;
  height: 100% !important;
}

.search-animation-wrapper {
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 35%;
  max-width: 450px;
  padding: 0;
  display: flex;
  position: relative;
  bottom: auto;
  right: auto;
}

.search-animation-container {
  background-color: var(--color-white);
  box-shadow: var(--box-shadow-light);
  text-align: center;
  opacity: 0;
  border-radius: 12px;
  width: 100%;
  padding: 15px;
  transform: translateY(20px);
}

.search-bar {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
  display: flex;
}

.search-input {
  border: 1px solid #ccc;
  border-radius: 20px;
  outline: none;
  flex-grow: 1;
  padding: 8px 12px;
  font-size: .9em;
  transition: border-color .3s;
}

.search-input:focus {
  border-color: var(--color-primary-blue);
  box-shadow: 0 0 0 3px #0054a633;
}

.search-button {
  background-color: var(--color-primary-blue);
  color: var(--color-white);
  cursor: pointer;
  border: none;
  border-radius: 20px;
  padding: 8px 18px;
  font-size: .9em;
  transition: background-color .3s, transform .2s;
}

.search-button:hover {
  background-color: var(--color-accent-green);
  transform: translateY(-2px);
}

.search-results {
  text-align: left;
  opacity: 0;
  height: 0;
  margin-top: 0;
  overflow: hidden;
  transform: translateY(20px);
}

.search-results h3 {
  color: var(--color-dark-navy-outline);
  font-family: var(--font-heading);
  margin-bottom: 5px;
  font-size: 1.1em;
}

.search-results p {
  color: var(--color-text-main);
  line-height: 1.4;
  font-family: var(--font-body);
  font-size: .8em;
}

.loading-indicator {
  color: #777;
  opacity: 0;
  margin-top: 8px;
  font-size: .8em;
}

.about-section {
  background: var(--color-white);
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 100px;
  padding: 80px 20px;
  display: flex;
}

.image-container img {
  max-width: 400px;
  transition: transform var(--transition-speed);
  border-radius: 50px;
  box-shadow: 0 0 5px #050505;
}

.image-container img:hover {
  transform: translateY(-5px);
}

.content-container {
  max-width: 500px;
}

.content-container h2 {
  color: var(--color-primary-blue);
  letter-spacing: normal;
  margin-bottom: 15px;
  font-size: 2rem;
}

.content-container p {
  font-family: Roboto, sans-serif;
}

.btn {
  background: var(--color-primary-blue);
  color: var(--color-white);
  transition: all var(--transition-speed);
  border-radius: 50px;
  margin-top: 40px;
  padding: 10px 20px;
  text-decoration: none;
  display: inline-block;
}

.btn:hover {
  background: var(--color-accent-green);
  color: var(--color-white);
  transform: translateY(-5px);
  box-shadow: 0 8px 20px #8dc63f66;
}

.dashed-line {
  display: none;
}

.services {
  text-align: center;
  color: var(--color-primary-blue);
  margin: 30px 0 10px;
  padding-top: 2rem;
  font-size: 2.25rem;
}

.services:after {
  content: "";
  background: var(--color-primary-blue);
  border-radius: 3px;
  width: 100px;
  height: 3px;
  margin: .1rem auto 0;
  display: block;
}

.cards {
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  padding: 40px 20px 100px;
  display: flex;
}

.card {
  background: linear-gradient(145deg, var(--color-white), #f8f8f8);
  text-align: center;
  border: 1px solid #0000000d;
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  max-width: 340px;
  padding: 35px;
  transition: transform .4s, box-shadow .4s, border-color .4s;
  display: flex;
  box-shadow: 0 10px 30px #0000001f;
}

.card:hover {
  border-color: var(--color-accent-green);
  transform: translateY(-10px);
  box-shadow: 0 20px 50px #00000040;
}

.card img {
  width: 125px;
  height: 125px;
  margin-bottom: 25px;
  padding: 10px;
}

.card:hover img {
  transform: scale(1.1)rotate(.5deg);
}

.card h3 {
  font-family: var(--font-heading);
  color: var(--color-primary-blue);
  margin-bottom: 15px;
  font-size: 1.6em;
}

.card p {
  color: var(--color-text-main);
  letter-spacing: .2px;
  flex-grow: 1;
  font-size: 1em;
  line-height: 1.7;
}

.card .btn {
  background-color: var(--color-accent-green);
  color: var(--color-white);
  text-transform: uppercase;
  letter-spacing: .5px;
  border-radius: 50px;
  margin-top: 0;
  padding: 10px 25px;
  font-size: .9em;
  font-weight: 600;
  box-shadow: 0 3px 10px #8dc63f4d;
}

.card .btn:hover {
  background-color: #7bb237;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px #8dc63f80;
}

.custom-footer {
  background: var(--color-dark-navy-outline);
  color: var(--color-white);
  padding: 40px 100px;
}

.footer-container {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 40px;
  display: flex;
}

.footer-section h4, .footer-section h3 {
  color: var(--color-accent-green);
  padding-bottom: 5px;
  font-size: 1.15rem;
}

.footer-section ul {
  gap: 5px;
  padding: 0;
  list-style: none;
}

.footer-section ul li a {
  color: var(--color-white);
  transition: color var(--transition-speed);
  margin-bottom: 5px;
  padding-bottom: 2px;
  font-size: .95rem;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.footer-section ul li {
  margin-bottom: 5px;
}

.footer-section ul li a:after {
  content: "";
  background-color: var(--color-accent-green);
  width: 0;
  height: 1px;
  transition: width var(--transition-speed);
  position: absolute;
  bottom: 0;
  left: 0;
}

.footer-section ul li a:hover {
  color: var(--color-accent-green);
}

.footer-section ul li a:hover:after {
  width: 100%;
}

.footer-logo {
  height: 150px;
}

.social-icons {
  padding-top: 10px;
}

.social-icons a {
  color: var(--color-white);
  transition: color var(--transition-speed), transform var(--transition-speed);
  margin-right: 10px;
  font-size: 1.2rem;
}

.social-icons a:hover {
  color: var(--color-accent-green);
  transform: scale(1.2)translateY(-3px);
}

.copyright {
  text-align: center;
  background: var(--color-primary-blue);
  color: var(--color-white);
  padding: 15px;
  font-size: .9rem;
}

@media (width >= 769px) {
  .hamburger-menu {
    display: none !important;
  }

  .floating-navbar-container .nav-links.active {
    display: flex;
  }

  .floating-navbar-container .contact-info-mobile {
    display: none !important;
  }
}

@media (width <= 768px) {
  header {
    box-shadow: var(--box-shadow-light);
    padding: 15px 20px;
  }

  .contact-info-desktop {
    display: none;
  }

  .floating-navbar-container {
    background: var(--color-primary-blue);
    border-radius: 10px;
    flex-direction: column;
    align-items: flex-start;
    max-width: calc(100% - 40px);
    margin: 10px 20px;
    padding: 12px 15px;
  }

  .floating-navbar-container nav {
    justify-content: flex-start;
    width: 100%;
  }

  .floating-navbar-container .nav-links {
    border-top: 1px solid #ffffff1a;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    margin-top: 10px;
    padding-top: 10px;
    display: none;
  }

  .floating-navbar-container .nav-links.active {
    display: flex;
  }

  .floating-navbar-container .contact-info-mobile {
    border-top: 1px solid #ffffff1a;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    margin-top: 10px;
    padding-top: 10px;
    display: none;
  }

  .floating-navbar-container .contact-info-mobile.active {
    display: flex;
  }

  .hamburger-menu {
    display: flex;
    position: absolute;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
  }

  .canvas-container {
    height: 600px;
    margin-top: 10px;
    padding-bottom: 15px;
  }

  .animation-content-wrapper {
    flex-direction: column;
    gap: 15px;
    padding: 15px;
  }

  canvas {
    max-width: 100%;
    height: 300px !important;
  }

  .search-animation-wrapper {
    justify-content: center;
    width: 100%;
    max-width: none;
  }

  .search-animation-container {
    padding: 10px;
  }

  .search-bar {
    flex-direction: column;
    gap: 8px;
  }

  .search-input, .search-button {
    width: 100%;
    padding: 6px 10px;
    font-size: .85em;
  }

  .search-results h3 {
    font-size: 1em;
  }

  .search-results p {
    font-size: .75em;
  }
}

@media (width <= 480px) {
  .logo {
    font-size: 1.1rem;
  }

  .logo img {
    height: 35px;
  }

  .floating-navbar-container {
    max-width: calc(100% - 20px);
    margin: 10px;
  }

  .floating-navbar-container .contact-info-mobile p {
    font-size: .8rem;
  }

  .cards {
    gap: 25px;
    padding: 20px 10px;
  }

  .card {
    padding: 25px;
  }

  .card img {
    width: 70px;
    height: 70px;
  }

  .card h3 {
    font-size: 1.4em;
  }
}

.about-main-content {
  padding: 20px 0;
}

.section-title-modern {
  font-family: var(--font-heading);
  color: var(--color-dark-navy-outline);
  text-align: center;
  background: linear-gradient(to right, var(--color-dark-navy-outline) 50%, var(--color-primary-blue) 50%);
  -webkit-text-fill-color: transparent;
  transition: background-position var(--transition-smooth-text-swipe);
  background-position: 0 0;
  background-size: 200% 100%;
  -webkit-background-clip: text;
  margin-bottom: 60px;
  padding-bottom: 20px;
  font-size: 2.8em;
  font-weight: 700;
  display: inline-block;
  position: relative;
}

.section-title-modern:hover {
  background-position: -100% 0;
}

.section-title-modern:after {
  content: "";
  background-color: var(--color-accent-green);
  border-radius: 2px;
  width: 120px;
  height: 3px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.section-title-modern-alt {
  font-family: var(--font-heading);
  color: var(--color-primary-blue);
  text-align: center;
  margin-bottom: 30px;
  padding-bottom: 15px;
  font-size: 2.2em;
  font-weight: 700;
  position: relative;
}

.section-title-modern-alt:after {
  content: "";
  background-color: var(--color-accent-green);
  border-radius: 1px;
  width: 80px;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.section-paragraph {
  color: var(--color-text-main);
  margin-bottom: 1.2em;
  font-size: 1.05em;
  line-height: 1.8;
}

.section-paragraph:last-child {
  margin-bottom: 0;
}

.section-paragraph-alt {
  color: var(--color-text-main);
  font-size: 1em;
  line-height: 1.7;
}

.about-hero-modern {
  background: radial-gradient(circle at top left, var(--color-primary-blue), var(--color-dark-navy-outline));
  width: 100%;
  height: 400px;
  box-shadow: var(--box-shadow-floating);
  justify-content: center;
  align-items: center;
  margin-bottom: 80px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.hero-bg-overlay {
  z-index: 1;
  background: radial-gradient(circle, #8dc63f26 0%, #0054a60d 50%, #0000 100%);
  width: 140%;
  height: 140%;
  animation: 50s linear infinite rotateGradient;
  position: absolute;
  top: -20%;
  left: -20%;
}

@keyframes rotateGradient {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hero-content-modern {
  z-index: 2;
  text-align: center;
  color: var(--color-white);
  text-shadow: 0 4px 10px #0000004d;
  max-width: 800px;
  padding: 0 20px;
  position: relative;
}

.modern-heading {
  font-family: var(--font-heading);
  letter-spacing: -.5px;
  margin-bottom: 15px;
  font-size: 3.8em;
  font-weight: 700;
  line-height: 1.1;
}

.modern-subheading {
  font-family: var(--font-body);
  opacity: .9;
  max-width: 600px;
  margin: 0 auto;
  font-size: 1.4em;
  font-weight: 300;
}

.about-intro-section {
  background-color: var(--color-white);
  border-radius: var(--border-radius);
  max-width: 1200px;
  box-shadow: var(--box-shadow-medium);
  flex-wrap: wrap;
  align-items: center;
  gap: 60px;
  margin: 0 auto 80px;
  padding: 60px;
  display: flex;
}

.intro-content-block, .intro-image-block {
  flex: 1;
  min-width: 300px;
}

.intro-image-block {
  justify-content: center;
  align-items: center;
  display: flex;
}

.responsive-image-modern {
  border-radius: var(--border-radius);
  max-width: 100%;
  height: auto;
  transition: transform var(--transition-smooth-secondary);
  box-shadow: 0 0 5px #050505;
}

.mv-modern-section {
  background: linear-gradient(135deg, var(--color-background-light), var(--color-secondary-bg));
  border-radius: var(--border-radius);
  max-width: 1200px;
  box-shadow: var(--box-shadow-medium);
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  margin: 0 auto 80px;
  padding: 60px;
  display: flex;
}

.mv-item {
  background-color: var(--color-white);
  border-radius: var(--border-radius);
  min-width: 380px;
  box-shadow: var(--box-shadow-light);
  text-align: center;
  transition: transform var(--transition-smooth-primary), box-shadow var(--transition-smooth-primary);
  flex: 1;
  padding: 40px;
  position: relative;
  overflow: hidden;
}

.mv-item:before {
  content: "";
  background: linear-gradient(to right, var(--color-primary-blue), var(--color-accent-green));
  transform-origin: 0;
  width: 100%;
  height: 5px;
  transition: transform var(--transition-smooth-secondary);
  position: absolute;
  top: 0;
  left: 0;
  transform: scaleX(0);
}

.mv-item:hover:before {
  transform: scaleX(1);
}

.mv-item:hover {
  box-shadow: var(--box-shadow-floating);
  transform: translateY(-10px);
}

.core-values-modern-section {
  background-color: var(--color-white);
  border-radius: var(--border-radius);
  max-width: 1200px;
  box-shadow: var(--box-shadow-medium);
  text-align: center;
  margin: 0 auto 80px;
  padding: 60px;
}

.values-grid-modern {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  justify-content: center;
  gap: 30px;
  margin: 0 auto;
  display: grid;
}

.value-item {
  background: var(--color-grey-light);
  border-radius: var(--border-radius);
  text-align: center;
  transition: all var(--transition-smooth-primary);
  border: 1px solid var(--color-grey-medium);
  padding: 35px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 3px 10px #0000000d;
}

.value-item:after {
  content: "";
  background-color: var(--color-accent-green);
  transform-origin: center;
  width: 100%;
  height: 4px;
  transition: transform var(--transition-smooth-secondary);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.value-item:hover {
  background-color: var(--color-secondary-bg);
  transform: translateY(-10px);
  box-shadow: 0 10px 30px #00000026;
}

.value-item:hover:after {
  transform: scaleX(1);
}

.value-icon-wrapper {
  background: linear-gradient(45deg, var(--color-primary-blue), var(--color-dark-navy-outline));
  width: 80px;
  height: 80px;
  box-shadow: var(--box-shadow-medium);
  transition: transform var(--transition-smooth-secondary), background var(--transition-smooth-secondary);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0 auto 25px;
  display: flex;
}

.value-item:hover .value-icon-wrapper {
  background: linear-gradient(45deg, var(--color-accent-green), #7bb237);
  transform: translateY(-5px)rotate(5deg);
}

.value-icon-wrapper i {
  color: var(--color-white);
  transition: color var(--transition-smooth-secondary);
  font-size: 2.8em;
}

.value-title {
  font-family: var(--font-body);
  color: var(--color-primary-blue);
  text-align: center;
  margin-bottom: 10px;
  font-size: 1.5em;
  font-weight: 600;
}

.value-description {
