.table-section:before, .table-section:after {
  content: "";
  opacity: 0;
  pointer-events: none;
  z-index: -1;
  background: linear-gradient(to right, #8dc63f00, #8dc63fb3, #8dc63f00);
  border-radius: 50%;
  width: 80px;
  height: 30px;
  position: absolute;
  top: 50%;
  left: -50%;
  transform: translate(-100%, -50%);
}

